.about{
    background: linear-gradient(30deg, #290A0A 20%, #938980 100% );
}
.aboutpic{
    width: 500px;
}

@media only screen and (max-width: 900px){
    .aboutpic{
        width: 400px;
    }

}

@media only screen and (max-width: 767px){
    .aboutpic{
        width: 350px;
    }

}

@media only screen and (max-width: 576px){
    
    .aboutpic{
        width: 300px;
    }

}