.home2{
    background: linear-gradient(60deg, #FFFFFF 40%, #C7E7F3 100% );
}

.home1{
    background-color: #EEEEEE;
}

.main{
    height:650px;
    display:flex;
    justify-content: center;
}

.firstpic{
    width: 430px;
    height: 550px;
}

.first{
    position: absolute;
    background-color: #CFD4D9;
    width: 430px;
    height: 530px;
    margin-top: 25px;
    margin-right: 20px;
}

.second{
    position: absolute;
    background-color: #CCC2B6;
    width: 430px;
    height: 530px;
    margin-top: 95px;
    margin-left: 80px;
}

.third{
    position: absolute;
    margin-top: 50px;
    margin-left: 30px;
}

@media only screen and (max-width: 576px){
   .first{
    width: 350px;
    margin-top: 0px;
    margin-right: 15px;
   }
   .second{
    width: 350px;
    margin-top: 60px;
    margin-left:50px;
   }
   .third{
    width: 350px;
    margin-top: 0px;
    margin-left: 0px;
   }
   .firstpic{
    width: 350px;
    margin-top: 20px;
    margin-left: 10px;
    }

}